* {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  background: radial-gradient(#11112b, #0e0e2b) 0 0 / cover no-repeat;
  margin: 0;
  font-family: Cambria, Cochin, Georgia, Times, Times New Roman, serif;
}

img {
  max-width: 100%;
}

.container {
  width: 100%;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.header-section {
  width: 100%;
  text-align: center;
  background-color: #6e6d80;
  border-bottom: 2px solid #141429;
  flex-direction: column;
  align-items: center;
  margin: 0 0 2em;
  padding: 1em 0 2em;
  display: flex;
  box-shadow: 2px 2px 2px #000;
}

.main-heading {
  color: #050531;
  text-shadow: 1px 1px 1px #000;
  margin: .5em 0;
  font-size: 2.3rem;
}

.search-form {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-label {
  font-size: 1.5rem;
}

#search-input {
  text-align: center;
  border: none;
  border-radius: 10px;
  margin: 0 1em;
  padding: .5em;
  font-size: 1.1rem;
}

.search-button, .details-button, .movie-details-close-btn {
  color: #080822;
  cursor: pointer;
  background-color: #c7b7b7;
  border: 1px solid #080822;
  border-radius: 10px;
  padding: .4em .7em;
  font-size: 1rem;
  font-weight: 700;
}

.search-button:focus, .search-button:hover, .details-button:focus, .details-button:hover, .movie-details-close-btn:focus, .movie-details-close-btn:hover {
  background-color: #aa9b9b;
}

.results-container {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1em;
  margin: 0 auto;
  padding: 1em;
  display: flex;
}

.movie-container {
  width: 18%;
  text-align: center;
  background-color: #7e7d8b;
  border: 1px solid #000;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  padding: 1em;
  display: flex;
}

.movie-image {
  margin-bottom: 1em;
}

.movie-title {
  width: 90%;
  word-wrap: break-word;
  margin: 0 auto;
}

.movie-year {
  margin: .5em 0;
  font-size: 1.4rem;
}

.footer-section {
  width: 100%;
  color: #fff;
  text-align: center;
  justify-content: center;
  margin: 1em;
  padding: 1em 0;
  display: flex;
}

.error-text {
  color: #a50606;
  background-color: #fff;
  padding: 1em;
}

.movie-details-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.movie-details-info {
  padding: 0 1em;
}

.movie-details-modal[open] {
  width: 50vw;
  color: #fff;
  background: radial-gradient(#11112b, #0e0e2b);
  border: 3px solid #fff;
  margin: 0 auto;
  position: fixed;
  top: 5em;
}

.movie-details-image {
  width: 50%;
}

@media (max-width: 950px) {
  .search-form {
    flex-direction: column;
  }

  .movie-details-modal[open] {
    width: 90vw;
    max-height: 100vh;
    top: 1em;
  }

  .movie-container {
    width: 90%;
  }

  .movie-details-container {
    flex-direction: column-reverse;
  }

  .results-container {
    max-width: 100%;
    margin: 0;
    padding: .5em;
  }

  #search-input {
    margin: 1em 0;
  }
}

